import React, { useState, useEffect, useRef } from 'react'
import { toast } from 'react-toastify';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Col } from 'reactstrap';

import visa from 'payment-icons/min/flat/visa.svg';
import amex from 'payment-icons/min/flat/amex.svg';
import mastercard from 'payment-icons/min/flat/mastercard.svg';

import stripeApi from '../../../../api/stripeApi'
import CardForm from './CardForm'

const CardSection = props => {
    const [cards, setCards] = useState([])
    const [defaultCard, setDefaultCard] = useState(null)

    const [showAdd, setShowAdd] = useState(false)
    useEffect(() => {
        loadData()
    }, [])

    const loadData = () => {
        stripeApi.getCustomer().then(data => {
            setDefaultCard(data.defaultSource)
            setCards(data.sources.data || [])
        })
    }

    const updateDefaultCard = async cardId => {
        try {
            const data = await stripeApi.updateDefaultCard(cardId)
            toast.success("Update default card success!");
            setShowAdd(false)
            setDefaultCard(data.defaultSource)
        } catch (error) {
            toast.error("Update default card fail!");
        }
    }

    const deleteCard = async cardId => {
        try {
            await stripeApi.deleteCard(cardId);
            toast.success("Delete card success!");
        } catch (error) {
            toast.error("Delete card fail!");
        }
        loadData()
    }

    const onSubmit = async payload => {
        if (payload.error) {
            toast.error(payload.error.message);
        }
        else {
            const cardToken = payload.token.id
            return stripeApi.addCard(cardToken).then(newCard => {
                setShowAdd(false)
                toast.success("Add card success!");
                loadData()
            }).catch(err => {
                toast.error("Add card fail!");
            })
        }
    }

    return <div className="card-list">
        <div className="d-flex justify-content-between mb-4">
            <div >
                Credit Card
            </div>
            <div>
                <img alt="amex" className="card-logo" src={amex} />
                <img alt="visa" className="card-logo ml-1" src={visa} />
                <img alt="mastercard" className="card-logo ml-1" src={mastercard} />
            </div>
        </div>
        <div className="mb-2">Pay with your credit card via Stripe.</div>
        <CardList
            cards={cards}
            defaultCard={defaultCard}
            onCardSelect={updateDefaultCard}
            onDelete={deleteCard}
            setShowAdd={setShowAdd}
        />
        {showAdd && <CardForm onSubmit={onSubmit} onCancel={() => setShowAdd(false)} />}
    </div>
}

const CardList = props => {
    const { cards, defaultCard, onCardSelect, onDelete, setShowAdd } = props;
    const [showDelete, setShowDelete] = useState(false);
    const [selectedId, setSelectedId] = useState(null);
    const [loading, setLoading] = useState(false)

    function deleteItem(cardId) {
        setShowDelete(true);
        setSelectedId(cardId);
    }

    async function performDelete() {
        setLoading(true)
        try {
            await onDelete(selectedId);
            setShowDelete(false);
        } catch (error) {

        } finally {
            setLoading(false)
        }
    }

    const selectedCard = cards.find(card => card.id === selectedId)

    const cardInfo = card => {
        if (!card) {
            return '';
        }

        return <>
            <span>{card.brand}</span> ending in {card.last4} (expires {card.expMonth}/{card.expYear})
        </>
    }

    return <>
        <ul>
            {cards.map(card => <li className="row" key={card.id}>
                <Col md="9">
                    <DisplayCard card={card} defaultCard={defaultCard} onChange={onCardSelect} />
                </Col>
                <Col md="3" className="text-right">
                    <Button color="link" className='p-0' onClick={e => deleteItem(card.id)}>Remove</Button>
                </Col>
            </li>)}
            <li className="row">
                <Col>
                    <div className="custom-control custom-radio">
                        <input
                            id="new-card-id"
                            className="custom-control-input"
                            type="radio"
                            name="card"
                            onClick={e => setShowAdd(true)}
                        ></input>
                        <label htmlFor="new-card-id" className="custom-control-label" onClick={e => setShowAdd(true)}>
                            Add payment method
                        </label>
                    </div>
                </Col>
            </li>
        </ul>
        <Modal color="danger" isOpen={showDelete} toggle={() => setShowDelete(!showDelete)}>
            <ModalHeader>Confirmation</ModalHeader>
            <ModalBody>
                <h4>Are you sure you want to delete this card?</h4>
                <b>{cardInfo(selectedCard)}</b>
            </ModalBody>
            <ModalFooter>
                <Button color="link" onClick={e => setShowDelete(false)}>Cancel</Button>
                <Button color="danger" onClick={performDelete} disabled={loading}>
                    {loading && <i className="fas fa-circle-notch fa-spin" />} Delete
                </Button>
            </ModalFooter>
        </Modal>
    </>
}

const DisplayCard = ({ card, defaultCard, onChange }) => {
    const cardInput = useRef()
    if (!card) {
        return '';
    }

    useEffect(() => {
        if (card.id === defaultCard) {
            cardInput.current.checked = true;
        }
    }, [card, defaultCard])

    const select = e => {
        if (e.target.value) {
            onChange(card.id)
        }
    }

    return <div className="custom-control custom-radio">
        <input
            className="custom-control-input"
            type="radio"
            name="card"
            id={card.id} value={card.id}
            // checked={checked}
            onChange={select}
            ref={cardInput}
        ></input>
        <label htmlFor={card.id} className="custom-control-label">
            {cardInfo(card)}
        </label>
    </div>
}

const cardInfo = card => {
    if (!card) {
        return '';
    }

    return <>
        <span>{card.brand}</span> ending in {card.last4} (expires {card.expMonth}/{card.expYear})
    </>
}

export default CardSection