import React from 'react';
import { Container, Card, CardHeader, CardBody } from 'reactstrap';

import CardSection from './CardSection'
import ConnectSection from './ConnectSection'

import './Billing.scss'

const Billing = props => {
    return (
        <Container className="my-billing">
            <Card className="mt-3">
                <CardHeader className="text-center">
                    <h2>EtheraPay Billing</h2>
                </CardHeader>
                <CardBody>
                    <CardSection />
                </CardBody>
            </Card>
            <Card>
                <CardHeader className="text-center">
                    <h2>Client Billing with Stripe Connect</h2>
                    <p>Please email <a href="mailto:support@myethera.com">support@myethera.com</a> if you have questions about setting up/linking your Stripe Connect account</p>
                </CardHeader>
                <CardBody>
                    <ConnectSection />
                </CardBody>
            </Card>
        </Container>
    );
}

export default Billing
