import React, { useEffect, useState } from 'react';
import { Button, Row, Col } from 'reactstrap';
import {
    CardNumberElement,
    CardExpiryElement,
    CardCVCElement,
    Elements,
    injectStripe,
} from 'react-stripe-elements';

import undefinedCard from 'payment-icons/min/flat/security-code.svg';
import visa from 'payment-icons/min/flat/visa.svg';
import amex from 'payment-icons/min/flat/amex.svg';
import mastercard from 'payment-icons/min/flat/mastercard.svg';

const createOptions = (fontSize, padding) => {
    return {
        style: {
            base: {
                fontSize,
                color: '#424770',
                letterSpacing: '0.025em',
                fontFamily: 'Source Code Pro, monospace',
                '::placeholder': {
                    color: '#aab7c4',
                },
            },
            invalid: {
                color: '#9e2146',
            },
        },
    };
};

const _CardForm = props => {
    const [loading, setLoading] = useState(false);
    const [accept, setAccept] = useState(false);
    const [cardType, setCardType] = useState(undefinedCard);

    const handleSubmit = async (ev) => {
        ev.preventDefault();
        if (props.stripe) {
            setLoading(true);
            const payload = await props.stripe.createToken();
            await props.onSubmit(payload)
            setLoading(false);
        } else {
            console.log("Stripe.js hasn't loaded yet.");
        }
    };

    function handleChangeNumber(change) {
        const { brand } = change
        switch (brand) {
            case 'visa':
                setCardType(visa)
                break;
            case 'amex':
                setCardType(amex)
                break;
            case 'mastercard':
                setCardType(mastercard)
                break;
            default:
                setCardType(undefinedCard)
                break
        }
    }

    return (
        <>
            <form onSubmit={handleSubmit} className="card-form">
                <div>
                    <div className="card-number-head d-flex justify-content-between">
                        <b>Card Number<sup className="text-required">*</sup></b>
                        <img alt="card type" src={cardType} />
                    </div>
                    <CardNumberElement
                        onChange={handleChangeNumber}
                        {...createOptions(props.fontSize)}
                    >
                    </CardNumberElement>
                </div>
                <Row>
                    <Col md={6}>
                        <b>Exp Date<sup className="text-required">*</sup></b>
                        <CardExpiryElement
                            {...createOptions(props.fontSize)}
                        />
                    </Col>
                    <Col md={6}>
                        <b>CVC<sup className="text-required">*</sup></b>
                        <CardCVCElement
                            {...createOptions(props.fontSize)}
                        />
                    </Col>
                </Row>
                <div className="custom-control custom-checkbox mb-3">
                    <input
                        className="custom-control-input"
                        id="accept-save"
                        type="checkbox"
                        onChange={e => setAccept(e.target.checked)}
                    />
                    <label
                        className="custom-control-label"
                        htmlFor="accept-save">
                        Save payment information.
                    </label>
                </div>
                <div>
                    <Button color="info" disabled={loading || !accept}>
                        <i className="fas fa-circle-notch fa-spin" hidden={!loading} /> Add
                    </Button>
                </div>
            </form>
        </>
    );
}

const CardForm = injectStripe(_CardForm)

const CardFormElement = props => {
    const [elementFontSize, setElementFontSize] = useState(window.innerWidth < 450 ? '14px' : '18px')
    useEffect(() => {
        const resizeEvent = window.addEventListener('resize', () => {
            if (window.innerWidth < 450 && elementFontSize !== '14px') {
                setElementFontSize('14px')
            } else if (window.innerWidth >= 450 && elementFontSize !== '18px') {
                setElementFontSize('18px')
            }
        });

        return function cleanUp() {
            window.removeEventListener('resize', resizeEvent)
        }
    }, [elementFontSize])
    return <Elements>
        <CardForm {...props} fontSize={elementFontSize} />
    </Elements>
}

export default CardFormElement