import React, { useState, useEffect } from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { toast } from 'react-toastify';

import config from '../../../../config'
import stripeApi from '../../../../api/stripeApi'

const ConnectSection = props => {

    const [stripeAccountId, setStripeAccountId] = useState(null);
    const [showDelete, setShowDelete] = useState(false);
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        loadData()
    }, [])

    const loadData = async () => {
        const data = await stripeApi.connectInfo() || {}
        setStripeAccountId(data.id)
    }

    const connectStripe = e => {
        const connectWindow = window.open(
            config.STRIPE_CONNECT_URL,
            "_blank",
            "toolbar=yes,scrollbars=yes,resizable=yes,top=200,left=200,width=800,height=500");

        const checker = setInterval(() => {
            if (connectWindow.closed) {
                loadData();
                clearInterval(checker)
            }
        }, 500);
    }

    const disconnectStripe = async e => {
        setLoading(true);
        try {
            await stripeApi.disconnectAccount()
            setShowDelete(false);
        } catch (error) {
            toast.error("Can not disconnect!")
        } finally {
            setLoading(false);
            loadData();
        }
    }

    return <>
        {stripeAccountId ?
            <>
                <p>You already linked with Stripe account: <b>{stripeAccountId}</b></p>
                <Button color="link" className="pl-0" onClick={e => setShowDelete(true)}>Remove Stripe Connect</Button>
            </>
            :
            <Button color="info" onClick={connectStripe}>Link Stripe Connect</Button>
        }
        <Modal color="danger" isOpen={showDelete} toggle={() => setShowDelete(!showDelete)}>
            <ModalHeader>Confirmation</ModalHeader>
            <ModalBody>
                <h4>Are you sure you want to remove Stripe Connect?</h4>
            </ModalBody>
            <ModalFooter>
                <Button color="link" onClick={e => setShowDelete(false)}>Cancel</Button>
                <Button color="danger" onClick={disconnectStripe} disabled={loading}>
                    {loading && <i className="fas fa-circle-notch fa-spin" />} Remove
                </Button>
            </ModalFooter>
        </Modal>
    </>
}

export default ConnectSection